/********************************************* BiensContainer ************************************************/
/************************** New rules if the screen is at more 1199px ****************************************/

@media screen and (min-width: 1200px) {

    /**********************************************************************************************/
    /**************************************** BiensContainer **************************************/
    /**********************************************************************************************/
    .biensCard {
        width: 100%;
        height: auto;
        margin: 0;
        font-family: 'Open', sans-serif;
    }

    .biensCard__header {
        width: 100%;
        height: 6vw;
        margin: 0;
    }

    .biensCard__header__logo {
        position: absolute;
        margin-top : 2vw;
		margin-left: 5vw;
		width: 8vw;
		height: auto;
        filter: drop-shadow(1px 3px 3px rgba(1, 1, 1, .35));
        z-index: 1000;
    }

    .biensCard__content {
        margin: 3vw 5vw auto 5vw;
        width: auto;
        height: auto;
        display: flex;
        justify-content: center;
        flex-direction: column;
    }

    .biensCard__content__tag {
        font-family: 'Open', sans-serif;
        margin-bottom: 3vw;
    }

    .biensCard__content__tag > li {
        display: inline-block;
        padding: .5vw .8vw .5vw .8vw;
        margin-right: .5vw;
        font-size: 1.5vw;
        letter-spacing: .2vw;
        border-radius: 5px;
        background-color: rgb(206, 39, 73);
        color: white;
    }

    #biensCard__content__tag__retour {
        font-weight: bold;
        letter-spacing: .15vw;
        background-color: rgb(0, 119, 255);
        cursor: pointer;
        transition: background-color .4s;
    }

    #biensCard__content__tag__retour:hover {
        background-color: rgb(75, 158, 253);
        transition: background-color .4s;
    }

    #biensCard__content__tag__retour:active {
        background-color: rgb(136, 191, 253);
    }

    #biensCard__content__tag__envoyer,
    #biensCard__content__tag__imprimer {
        float: right;
        margin-left: .5vw;
        margin-right: 0;
        background-color: rgb(122, 122, 122);
        cursor: pointer;
        transition: background-color .4s;
    }

    #biensCard__content__tag__envoyer:hover,
    #biensCard__content__tag__imprimer:hover {
        background-color: rgb(155, 155, 155);
        transition: background-color .4s;
    }

    .biensCard__content__text {
        display: flex;
        width: auto;
        height: auto;
        padding: 0;
        margin-bottom: 5vw;
        font-size: 1.3vw;
        line-height: 2vw;
        color: rgb(75, 75, 75);
    }

    .biensCard__content__text__description {
        position: relative;
        width: 100%;
        height: auto;
        padding: 1vw 1vw 1vw 0;
    }

    .biensCard__content__text__description > header {
        padding: 0 .5vw 0 .5vw;
    }

    .biensCard__content__text__description > p {
        display: inline-block;
        padding: 0 .2vw 0 .7vw;
        width: 100%;
        height: auto;
        text-align: justify;
        text-justify: distribute;
    }

    .biensCard__content__text__description > header {
        background-color: transparent;
        line-height: 0;
        font-weight: 600;
        letter-spacing: .15vw;
    }

    .biensCard__content__text__description__title {
        display: flex;
        justify-content: center;
        flex-direction: column;
        flex-wrap: wrap;
    }

    .biensCard__content__text__description__title > li > div {
        min-width: 43.2vw;
        margin-bottom: 2vw;
        padding-bottom: 3.5vw;
        border-radius: 5px;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        overflow: hidden;
    }

    .biensCard-dropdown--Up {
        max-height: 4vw;
        transition: max-height .5s;
    }

    .biensCard-dropdown--Down {
        max-height: 48vw;
        height: auto;
        transition: max-height .5s;
    }

    .biensCard__content__text__description__title > li > div > header {
        border-radius: 5px 5px 0 0;
    }

    .biensCard__content__text__description__title > li > div > header > p {
        background-color: rgb(206, 39, 73);
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        color: white;
        padding: 1vw;
        margin-top: 0;
        margin-bottom: 0;
        border-radius: 5px;
        font-size: 1.8vw;
        cursor: pointer;
        z-index: 2000;
    }

    .biensCard__content__text__description__title > li > div > header > p > span {
        float: right;
        margin-right: 1vw;
    }

    .biensCard__content__text__description__title > li > div > p,
    .barem {
        margin-top: 2.5vw;
        margin-left: 1vw;
        margin-right: 2vw;
        line-height: .8vw;
    }

    .barem {
        color: rgb(0, 119, 255) !important;
    }

    .barem:hover {
        text-decoration: underline !important;
    }

    .biensCard__content__text__description__title > li > div > p > span {
        float: right;
    }

    .biensCard__content__text__description__title > li > div > div {
        display: flex;
        flex-direction: column;
        margin: .7vw 0 0 1vw;
    }

    .biensCard__content__text__description__title > li > div > div > img {
        margin: 0;
        width: 11vw;
        height: auto;
    }

    .biensCard-button-container {
        position: relative;
        text-align: center;
        margin: 0 auto;
        width: 100%;
    }
    .biensCard-button-container__simulateur {
        margin-right: .7vw;
    }

    .biensCard-button-container__contact {
        display: inline-block;
    }
}
