/********************************************* BiensContainer ************************************************/
/************************** New rules if the screen is at more 1199px ****************************************/

@media screen and (min-width: 1200px) {

    /**********************************************************************************************/
    /**************************************** BiensContainer **************************************/
    /**********************************************************************************************/
    
    .biensContainer {
        text-align: center;
    }

    .biensContainer__vide {
        margin: 5vw 0 0 0;
    }

    .biensContainer__button {
        margin: 3vw 0 0 0;
    }

    .biensContainer__select {
        color: black;
        font-family: 'Open', sans-serif;
        margin-bottom: 3vw;
    }

    .biensContainer__select__label {
        font-size: 1.3vw;
        font-weight: bold;
    }

    .biensContainer__select__option {
        text-align: center;
        padding: .7vw;
        border: 1px solid #ccc;
        border-radius: 5px;
    }

    .biensContainer__select__option > option {
        font-size: 1vw;
    }

    .biensContainer__items {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        margin: auto;
        padding: 5vw;
        width: 100%;
    }

    .biensContainer__items__items {
        position: relative;
        width: 28vw;
        height: 18.5vw;
        margin-bottom: 2.5vw;
        border-radius: 20px;
        cursor: pointer;
        overflow: hidden;
        background-color: black;
    }

    .biensContainer__items__items__covers {
        width: 28vw;
        height: auto;
        transition: width .4s;
        opacity: .5;
        z-index: -1000;
    }

    .biensContainer__items__items:hover>.biensContainer__items__items__covers,
    .biensContainer__items__items__exclu .biensContainer__items__items__covers {
        width: 31vw;
        transition: width .4s
    }

    .biensContainer__items__items__exclu {
        position: absolute;
        display: block;
        width: 9vw;
        margin-top: 1.5vw;
        padding: .51vw;
        font-family: 'Open', sans-serif;
        font-size: 1.3vw;
        letter-spacing: .073vw;
        color: white;
        background-color: rgb(206, 39, 73);
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
        z-index: 1000;
    }

    .biensContainer__items__items__content {
        position: absolute;
        text-align: center;
        bottom: 2vw;
        width: 100%;
        font-family: 'Open', sans-serif;
        color: white !important;
        letter-spacing: .036vw; 
        line-height: 1.4vw;
        padding: 0 1.3vw 1.3vw 1.3vw;
        text-shadow: 0 0 .2em rgb(1, 1, 1), 0 0 0.1em rgb(1, 1, 1);
        z-index: 1000;
    }

    .biensContainer__items__items__content__secteur {
        font-size: 1.3vw;
        display: block; 
        display: -webkit-box;
        height: 2.6em; 
        line-height: 1.3em;
        -webkit-line-clamp: 2; 
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .biensContainer__items__items__content__prix {
        font-size: 1.5vw;
        font-weight: bold;
        line-height: 0;
    }

    .biensContainer__items__items__content__description {
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        font-size: 1.3vw;
    }

    .biensContainer__contentVente, .biensContainer__contentAchat {
        font-family: 'Open', sans-serif;
        font-size: 1.3vw;
        line-height: 2vw;
        color: rgb(75, 75, 75);
        margin: -2vw auto 5vw auto;
    }
}
